"use client"

import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Separator } from "@/components/ui/separator"
import { useState } from "react"
import { useRouter } from "next/navigation"

interface FormData {
  // Motor details
  nombre: string
  numeroSerie: string
  marca: string
  modelo: string
  potencia: string
  voltaje: string
  amperaje: string
  rpm: string
  tipo: string
  ubicacion: string
  
  // Maintenance details
  fechaMantenimiento: string
  tipoMantenimiento: string
  descripcion: string
  recomendaciones: string
  proximoMantenimiento: string
  
  // Personnel details
  tecnicoResponsable: string
  supervisor: string
  departamento: string
  
  // Images
  imagenesAntes: File[]
  imagenesDespues: File[]
}

export default function NuevoMotor() {
  const router = useRouter()
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [activeTab, setActiveTab] = useState("motor")
  
  const [formData, setFormData] = useState<FormData>({
    nombre: "",
    numeroSerie: "",
    marca: "",
    modelo: "",
    potencia: "",
    voltaje: "",
    amperaje: "",
    rpm: "",
    tipo: "",
    ubicacion: "",
    fechaMantenimiento: "",
    tipoMantenimiento: "",
    descripcion: "",
    recomendaciones: "",
    proximoMantenimiento: "",
    tecnicoResponsable: "",
    supervisor: "",
    departamento: "",
    imagenesAntes: [],
    imagenesDespues: []
  })

  const validateForm = () => {
    const errors = []
    
    // Motor validation
    if (!formData.nombre.trim()) errors.push('El nombre del motor es requerido')
    if (!formData.numeroSerie.trim()) errors.push('El número de serie es requerido')
    if (!formData.marca.trim()) errors.push('La marca es requerida')
    if (!formData.modelo.trim()) errors.push('El modelo es requerido')
    
    // Maintenance validation
    if (!formData.fechaMantenimiento) errors.push('La fecha de mantenimiento es requerida')
    if (!formData.tipoMantenimiento.trim()) errors.push('El tipo de mantenimiento es requerido')
    if (!formData.descripcion.trim()) errors.push('La descripción es requerida')
    
    // Personnel validation
    if (!formData.tecnicoResponsable.trim()) errors.push('El técnico responsable es requerido')
    if (!formData.supervisor.trim()) errors.push('El supervisor es requerido')
    
    // Images validation
    if (formData.imagenesAntes.length === 0) errors.push('Se requiere al menos una imagen antes del mantenimiento')
    if (formData.imagenesDespues.length === 0) errors.push('Se requiere al menos una imagen después del mantenimiento')

    if (errors.length > 0) {
      setError(errors.join('\n'))
      return false
    }
    return true
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError(null)
    
    if (!validateForm()) {
      return
    }

    setLoading(true)
    
    try {
      const formDataToSend = new FormData()
      
      // Append all form fields
      Object.entries(formData).forEach(([key, value]) => {
        if (key !== 'imagenesAntes' && key !== 'imagenesDespues') {
          formDataToSend.append(key, value.toString().trim())
        }
      })
      
      // Append images
      formData.imagenesAntes.forEach((file) => {
        formDataToSend.append('imagenesAntes', file)
      })
      
      formData.imagenesDespues.forEach((file) => {
        formDataToSend.append('imagenesDespues', file)
      })

      const response = await fetch('/api/motores', {
        method: 'POST',
        body: formDataToSend,
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Error al guardar el motor')
      }

      router.push('/')
    } catch (error) {
      setError(error instanceof Error ? error.message : 'Error al guardar el motor')
    } finally {
      setLoading(false)
    }
  }

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>, type: "antes" | "despues") => {
    if (e.target.files && e.target.files.length > 0) {
      const files = Array.from(e.target.files)
      setFormData(prev => ({
        ...prev,
        [type === "antes" ? "imagenesAntes" : "imagenesDespues"]: files
      }))
    }
  }

  return (
    <div className="max-w-4xl mx-auto py-8">
      <Card className="border border-gray-200 bg-white/90 backdrop-blur-sm shadow-xl hover:shadow-2xl transition-all duration-300">
        <CardHeader className="bg-gradient-to-r from-gray-50 to-white border-b relative overflow-hidden">
          <div className="absolute inset-0 bg-gradient-to-r from-gray-200 to-transparent opacity-0 hover:opacity-10 transition-opacity duration-300"></div>
          <CardTitle className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-gray-900 to-gray-600 relative z-10">
            Registro de Mantenimiento de Motor
          </CardTitle>
        </CardHeader>
        <CardContent className="p-6">
          <form onSubmit={handleSubmit} className="space-y-6" noValidate>
            {error && (
              <div className="bg-red-50 border-l-4 border-red-400 p-4 rounded-md mb-6">
                <div className="flex">
                  <div className="ml-3">
                    <h3 className="text-sm font-medium text-red-800">Por favor corrija los siguientes errores:</h3>
                    <div className="mt-2 text-sm text-red-700">
                      <ul className="list-disc pl-5 space-y-1">
                        {error.split('\n').map((err, index) => (
                          <li key={index}>{err}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            )}

            <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-4">
              <TabsList className="grid w-full grid-cols-4 bg-gradient-to-r from-gray-50 to-white rounded-lg p-1 shadow-inner">
                <TabsTrigger value="motor" className="data-[state=active]:bg-white data-[state=active]:shadow-md hover:bg-white/80 transition-all font-medium relative overflow-hidden group">
                  <span className="relative z-10">Datos del Motor</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-gray-200 to-transparent opacity-0 group-hover:opacity-20 transition-opacity duration-300"></div>
                </TabsTrigger>
                <TabsTrigger value="maintenance" className="data-[state=active]:bg-white data-[state=active]:shadow-md hover:bg-white/80 transition-all font-medium relative overflow-hidden group">
                  <span className="relative z-10">Mantenimiento</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-gray-200 to-transparent opacity-0 group-hover:opacity-20 transition-opacity duration-300"></div>
                </TabsTrigger>
                <TabsTrigger value="personnel" className="data-[state=active]:bg-white data-[state=active]:shadow-md hover:bg-white/80 transition-all font-medium relative overflow-hidden group">
                  <span className="relative z-10">Personal</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-gray-200 to-transparent opacity-0 group-hover:opacity-20 transition-opacity duration-300"></div>
                </TabsTrigger>
                <TabsTrigger value="images" className="data-[state=active]:bg-white data-[state=active]:shadow-md hover:bg-white/80 transition-all font-medium relative overflow-hidden group">
                  <span className="relative z-10">Imágenes</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-gray-200 to-transparent opacity-0 group-hover:opacity-20 transition-opacity duration-300"></div>
                </TabsTrigger>
              </TabsList>

              <TabsContent value="motor" className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="nombre">Nombre del Motor</Label>
                    <Input
                      id="nombre"
                      value={formData.nombre}
                      onChange={(e) => setFormData(prev => ({ ...prev, nombre: e.target.value }))}
                      placeholder="Ej: Motor Principal Línea 1"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="numeroSerie">Número de Serie</Label>
                    <Input
                      id="numeroSerie"
                      value={formData.numeroSerie}
                      onChange={(e) => setFormData(prev => ({ ...prev, numeroSerie: e.target.value }))}
                      placeholder="Ej: SN2024001"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="marca">Marca</Label>
                    <Input
                      id="marca"
                      value={formData.marca}
                      onChange={(e) => setFormData(prev => ({ ...prev, marca: e.target.value }))}
                      placeholder="Ej: Siemens"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="modelo">Modelo</Label>
                    <Input
                      id="modelo"
                      value={formData.modelo}
                      onChange={(e) => setFormData(prev => ({ ...prev, modelo: e.target.value }))}
                      placeholder="Ej: 1LA7"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="potencia">Potencia</Label>
                    <Input
                      id="potencia"
                      value={formData.potencia}
                      onChange={(e) => setFormData(prev => ({ ...prev, potencia: e.target.value }))}
                      placeholder="Ej: 75 HP"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="voltaje">Voltaje</Label>
                    <Input
                      id="voltaje"
                      value={formData.voltaje}
                      onChange={(e) => setFormData(prev => ({ ...prev, voltaje: e.target.value }))}
                      placeholder="Ej: 440V"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="amperaje">Amperaje</Label>
                    <Input
                      id="amperaje"
                      value={formData.amperaje}
                      onChange={(e) => setFormData(prev => ({ ...prev, amperaje: e.target.value }))}
                      placeholder="Ej: 150A"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="rpm">RPM</Label>
                    <Input
                      id="rpm"
                      value={formData.rpm}
                      onChange={(e) => setFormData(prev => ({ ...prev, rpm: e.target.value }))}
                      placeholder="Ej: 1750"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="tipo">Tipo de Motor</Label>
                    <Input
                      id="tipo"
                      value={formData.tipo}
                      onChange={(e) => setFormData(prev => ({ ...prev, tipo: e.target.value }))}
                      placeholder="Ej: Trifásico"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="ubicacion">Ubicación</Label>
                    <Input
                      id="ubicacion"
                      value={formData.ubicacion}
                      onChange={(e) => setFormData(prev => ({ ...prev, ubicacion: e.target.value }))}
                      placeholder="Ej: Planta 1 - Línea de Producción"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                </div>
              </TabsContent>

              <TabsContent value="maintenance" className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="fechaMantenimiento">Fecha de Mantenimiento</Label>
                    <Input
                      id="fechaMantenimiento"
                      type="date"
                      value={formData.fechaMantenimiento}
                      onChange={(e) => setFormData(prev => ({ ...prev, fechaMantenimiento: e.target.value }))}
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="tipoMantenimiento">Tipo de Mantenimiento</Label>
                    <Input
                      id="tipoMantenimiento"
                      value={formData.tipoMantenimiento}
                      onChange={(e) => setFormData(prev => ({ ...prev, tipoMantenimiento: e.target.value }))}
                      placeholder="Ej: Preventivo, Correctivo"
                    />
                  </div>
                  <div className="col-span-2 space-y-2">
                    <Label htmlFor="descripcion">Descripción del Mantenimiento</Label>
                    <Textarea
                      id="descripcion"
                      value={formData.descripcion}
                      onChange={(e) => setFormData(prev => ({ ...prev, descripcion: e.target.value }))}
                      placeholder="Detalle las actividades realizadas durante el mantenimiento"
                      rows={4}
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300 resize-none"
                    />
                  </div>
                  <div className="col-span-2 space-y-2">
                    <Label htmlFor="recomendaciones">Recomendaciones</Label>
                    <Textarea
                      id="recomendaciones"
                      value={formData.recomendaciones}
                      onChange={(e) => setFormData(prev => ({ ...prev, recomendaciones: e.target.value }))}
                      placeholder="Indique recomendaciones para futuros mantenimientos"
                      rows={3}
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300 resize-none"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="proximoMantenimiento">Próximo Mantenimiento</Label>
                    <Input
                      id="proximoMantenimiento"
                      type="date"
                      value={formData.proximoMantenimiento}
                      onChange={(e) => setFormData(prev => ({ ...prev, proximoMantenimiento: e.target.value }))}
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                </div>
              </TabsContent>

              <TabsContent value="personnel" className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="tecnicoResponsable">Técnico Responsable</Label>
                    <Input
                      id="tecnicoResponsable"
                      value={formData.tecnicoResponsable}
                      onChange={(e) => setFormData(prev => ({ ...prev, tecnicoResponsable: e.target.value }))}
                      placeholder="Nombre del técnico"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="supervisor">Supervisor</Label>
                    <Input
                      id="supervisor"
                      value={formData.supervisor}
                      onChange={(e) => setFormData(prev => ({ ...prev, supervisor: e.target.value }))}
                      placeholder="Nombre del supervisor"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="departamento">Departamento</Label>
                    <Input
                      id="departamento"
                      value={formData.departamento}
                      onChange={(e) => setFormData(prev => ({ ...prev, departamento: e.target.value }))}
                      placeholder="Ej: Mantenimiento Industrial"
                      className="bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300"
                    />
                  </div>
                </div>
              </TabsContent>

              <TabsContent value="images" className="space-y-6">
                <div className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="imagenesAntes">Imágenes Antes del Mantenimiento</Label>
                    <Input
                      id="imagenesAntes"
                      type="file"
                      accept="image/*"
                      multiple
                      onChange={(e) => handleImageChange(e, "antes")}
                      className="border-2 border-dashed p-4 bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300 cursor-pointer file:bg-black file:text-white file:border-0 file:rounded-md file:px-4 file:py-2 file:mr-4 file:hover:bg-gray-800 file:transition-colors"
                    />
                    {formData.imagenesAntes.length > 0 && (
                      <p className="text-sm text-gray-500">
                        {formData.imagenesAntes.length} imágenes seleccionadas
                      </p>
                    )}
                  </div>

                  <Separator />

                  <div className="space-y-2">
                    <Label htmlFor="imagenesDespues">Imágenes Después del Mantenimiento</Label>
                    <Input
                      id="imagenesDespues"
                      type="file"
                      accept="image/*"
                      multiple
                      onChange={(e) => handleImageChange(e, "despues")}
                      className="border-2 border-dashed p-4 bg-gradient-to-r from-gray-50 to-white hover:from-white hover:to-white focus:from-white focus:to-white transition-all duration-300 cursor-pointer file:bg-black file:text-white file:border-0 file:rounded-md file:px-4 file:py-2 file:mr-4 file:hover:bg-gray-800 file:transition-colors"
                    />
                    {formData.imagenesDespues.length > 0 && (
                      <p className="text-sm text-gray-500">
                        {formData.imagenesDespues.length} imágenes seleccionadas
                      </p>
                    )}
                  </div>
                </div>
              </TabsContent>
            </Tabs>

            <div className="flex justify-end space-x-4 pt-6 border-t">
              <Button 
                variant="outline" 
                type="button" 
                onClick={() => router.push("/")}
                disabled={loading}
                className="hover:bg-black/5"
              >
                Cancelar
              </Button>
              <Button 
                type="submit"
                disabled={loading}
                      className="min-w-[120px] bg-gradient-to-r from-gray-900 to-gray-700 hover:from-gray-800 hover:to-gray-600 text-white shadow-lg hover:shadow-xl transition-all"
              >
                {loading ? (
                  <div className="flex items-center justify-center">
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></div>
                    <span>Guardando...</span>
                  </div>
                ) : (
                  "Guardar Motor"
                )}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
      <div className="mt-12 text-center border-t pt-8">
        <div className="inline-block relative">
          <div className="bg-gradient-to-r from-transparent via-gray-100 to-transparent py-4 px-8 rounded-lg shadow-lg">
            <p className="text-base text-gray-600">
              Desarrollado por{" "}
              <span className="bg-gradient-to-r from-gray-900 via-gray-700 to-gray-900 bg-clip-text text-transparent font-bold text-xl tracking-wider hover:scale-105 transition-all duration-300 inline-block relative group">
                MURFI PROGRAMMING
                <span className="absolute -inset-1 bg-gradient-to-r from-gray-200 via-white to-gray-200 opacity-0 group-hover:opacity-100 blur transition-all duration-300"></span>
              </span>
            </p>
            <div className="h-0.5 w-full bg-gradient-to-r from-transparent via-gray-400 to-transparent mt-2 animate-pulse"></div>
          </div>
        </div>
      </div>
    </div>
  )
}
